import React, { useState, useMemo } from 'react';
import { Link } from 'react-router-dom';
import { columns, outTimeColumn, deliveryStatusColumn, searchTabType } from '../../config';
import TableList from '@/components/TableList';
import { getPrescriptionList } from '@/services/prescription';

export type TableListProps = {
  type: string;
  searchText: string;
  reloadRandom: string;
  paramsRandom?: number;
};

const getColumns = (type: string, timeOrders: any, amountOrders: any): any[] => {
  const newColumns = [...columns];
  const column: any = type === 'waitCount' ? outTimeColumn : deliveryStatusColumn;
  const index = newColumns.map((item) => item.key).indexOf('prescriptionAmount');
  newColumns.splice(index, 0, column);
  newColumns[newColumns.length - 1].render = (text: string, record: any) => (
    // @ts-ignore
    <Link to={`/prescrip/order/reviewDetail?id=${record.id}&type=${searchTabType[type]}&from=out`}>
      查看详情
    </Link>
  );
  return newColumns;
};
function DefaultTableList(props: TableListProps) {
  const { type, searchText, reloadRandom, paramsRandom } = props;
  const [timeOrders, setTimeOrders] = useState<any[]>([]);
  const [amountOrders, setAmountOrders] = useState<any[]>([]);
  const columnsData = useMemo(
    () => getColumns(type, timeOrders[0], amountOrders[0]),
    [type, timeOrders, amountOrders],
  );
  const params = useMemo(
    () => ({
      paramsRandom,
      // @ts-ignore
      entity: { selectTab: searchTabType[type], simpleSearch: searchText },
      orders: [...timeOrders, ...amountOrders],
    }),
    [searchText, type, timeOrders, amountOrders, paramsRandom],
  );
  const onChange = (pagination: any, filters: any, sorter: any, extra: any) => {
    if (extra.action === 'sort') {
      if (sorter.field === 'createTime') {
        if (sorter.order === undefined) {
          setTimeOrders([]);
          return;
        }
        setTimeOrders([
          { property: 'create_time', direction: sorter.order === 'ascend' ? 'asc' : 'desc' },
        ]);
      }
    }
  };
  return (
    <TableList
      params={params}
      fetch={getPrescriptionList}
      columns={columnsData}
      reload={reloadRandom}
      onChange={onChange}
    />
  );
}

export default DefaultTableList;
