import { Table, Badge, TableColumnProps } from '@sinohealth/butterfly-ui-components/lib';
import moment from 'moment';
import { Link } from 'react-router-dom';

const o2o = [0, 5, 10, 15].reverse();
const b2c = [0, 12, 36, 60].reverse();
export const preStatusText = ['新处方', '超时处方', '超时处方', '超时处方'].reverse();
export const preStatusColor = ['#7ED321', '#FFC100', '#FF6100', '#F53F3F'].reverse();
export const getPreStatus = (time: string, type: string) => {
  const step = type === 'O2O' ? 'minutes' : 'hours';
  const data = type === 'O2O' ? o2o : b2c;
  const timeLeft = moment(Date.now()).diff(time, step);
  let i: number = -1;
  data.forEach((item, index) => {
    if (i === -1 && timeLeft >= item) {
      i = index;
    }
  });
  return i;
};
export const getTimeLeft = (time: string, type: string, noStyle = false) => {
  const step = type === 'O2O' ? 'minutes' : 'hours';
  const stepName = type === 'O2O' ? '分钟' : '小时';
  const timeLeft = moment(Date.now()).diff(time, step);
  const status = getPreStatus(time, type);
  if (noStyle) {
    return status === 3 ? '新处方' : `超时${timeLeft}${stepName}`;
  }
  return (
    <span style={{ color: preStatusColor[status] }}>
      {status === 3 ? '新处方' : `超时${timeLeft}${stepName}`}
    </span>
  );
};
