import React, { useState, useEffect } from 'react';
import { Row, Col, Tag, Pagination, Button } from '@sinohealth/butterfly-ui-components/lib';
import { ClockCircleOutlined, WomanOutlined, ManOutlined } from '@ant-design/icons';
import moment from 'moment';
import { useNavigate } from 'react-router-dom';
import useSWR from 'swr';
import style from './index.less';
import {
  searchTabType,
  preStatusColor,
  getPreStatus,
  getTimeLeft,
} from '@/pages/prescription/prescriptionReviewList/config';
import Empty from '@/components/Empty';
import { getPrescriptionList } from '@/services/prescription';

function CardList(props: { searchText: string; reloadRandom: string; paramsRandom: number }) {
  const { searchText = '', reloadRandom, paramsRandom } = props;
  const navigate = useNavigate();
  const [page, setPage] = useState(1);
  const [pageSize, setPageSize] = useState(20);
  const { data, mutate } = useSWR<Prescription.ListResponse, any>(
    {
      pageNo: page,
      pageSize,
      paramsRandom,
      entity: { selectTab: searchTabType.waitCount, simpleSearch: searchText },
    },
    getPrescriptionList,
  );
  useEffect(() => {
    mutate();
  }, [reloadRandom]);
  const toDetail = (id: string | number) => {
    navigate(`/prescrip/order/reviewDetail?id=${id}&type=${searchTabType.waitCount}&from=out`);
  };
  const renderActionBar = (status: any, timeLeftText: any, id: number) => (
    <div className={style.actionBar}>
      {status !== 3 && (
        <div className={style.longTime}>
          <ClockCircleOutlined style={{ color: preStatusColor[status] }} />
          &nbsp; {timeLeftText}
        </div>
      )}
      <div className={style.text} onClick={() => toDetail(id)}>
        去审方
      </div>
    </div>
  );
  const renderCardItem = (item: Prescription.Item) => {
    const isWoman = item.gender === 'female';
    const prsStatus = getPreStatus(item.createTime, item.deliveryCategory);
    const timeLeftText = getTimeLeft(item.createTime, item.deliveryCategory, true);
    const age = moment().diff(item.birthDate, 'year');
    const tagNameKey = `tag${prsStatus}`;
    return (
      <div className={style.listItem}>
        <Tag className={[style.tag, style[tagNameKey]].join(' ')}>{timeLeftText}</Tag>
        <div>
          <div className={style.userInfo}>
            <div className={style.userName}>
              {item.patientName}
              &nbsp;
              {isWoman ? (
                <WomanOutlined style={{ fontSize: '16px', color: '#FFCED9' }} />
              ) : (
                <ManOutlined style={{ fontSize: '16px', color: '#B1C1F7' }} />
              )}
            </div>
            <div>
              {age}岁， {item.patientPhone}
            </div>
          </div>
          <div className={style.preInfo}>
            <div>
              订单类型：<span className={style.text}>{item.deliveryCategory}</span>
            </div>
            <div>
              处方金额：<span className={style.text}>¥{item.prescriptionAmount}</span>
            </div>
            <div>
              订单时间：<span className={style.text}>{item.createTime}</span>
            </div>
          </div>
          <Button block type="primary" onClick={() => toDetail(item.id)}>
            去审方
          </Button>
        </div>
      </div>
    );
  };
  const onChange = (e: any, size: number) => {
    setPage(e);
    setPageSize(size);
  };
  if (data?.records.length === 0) {
    return (
      <div style={{ display: 'flex' }}>
        <Empty isSearch={searchText.length > 0} />
      </div>
    );
  }

  return (
    <div className={style.cardList}>
      <Row gutter={[18, 20]}>
        {data?.records.map((item) => (
          <Col lg={6} xxl={4} key={item.id}>
            {renderCardItem(item)}
          </Col>
        ))}
      </Row>
      <div className={style.paginationBox}>
        <Pagination
          showQuickJumper
          showSizeChanger
          showTotal={(total) => `共${total}条数据`}
          current={page}
          pageSize={pageSize}
          total={data?.total}
          pageSizeOptions={[20, 40]}
          onChange={onChange}
        />
      </div>
    </div>
  );
}

export default CardList;
