import React, { useState } from 'react';
import { Table } from '@sinohealth/butterfly-ui-components/lib';
import { CaretDownOutlined, CaretUpOutlined } from '@ant-design/icons';
import { drugListColumns } from '@/pages/prescription/prescriptionReviewList/config';
import style from './index.less';

function PTableList(props: { listData: Prescription.drugItem[] }) {
  const { listData = [] } = props;
  const tableTitle = (
    <div style={{ paddingLeft: '32px' }} onClick={() => setExpand(!expand)}>
      处方明细（{listData.length}）<CaretDownOutlined />
    </div>
  );
  // @ts-ignore
  drugListColumns[0].title = tableTitle;
  const [expand, setExpand] = useState(false);
  const tableDom = (
    <div>
      <Table
        columns={drugListColumns}
        dataSource={listData}
        bordered={false}
        pagination={false}
        rowKey={(record) => Date.now()}
      />
    </div>
  );
  const textDom = (
    <div className={style.abbreviationDom}>
      <div className={style.title} onClick={() => setExpand(!expand)}>
        处方明细（{listData.length}）<CaretUpOutlined />
      </div>
      <div className={style.label}>
        <span className={style.drugName}>{listData.map((item) => item.name).join(',')}</span>等
        {listData.length}项药品
      </div>
    </div>
  );
  return expand ? tableDom : textDom;
}
export default PTableList;
