import React, { useState, useEffect, useRef, useMemo } from 'react';
import {
  Card,
  Form,
  Row,
  Col,
  Button,
  Tabs,
  Radio,
  Modal,
  message,
} from '@sinohealth/butterfly-ui-components/lib';
import { SyncOutlined, TableOutlined, BarsOutlined, QuestionCircleFilled } from '@ant-design/icons';
import useSWR from 'swr';
import SearchInput from '@/components/SearchInput/index';
import PrescriptionCount from './components/PrescriptionCount';
import DefaultTableList from './components/DefaultTableList';
import PTableList from './components/PTableList';
import CardList from './components/CardList';
import { tabs, defaultPrTotalData } from './config';
import style from './index.less';
import { batchDelivery, getPrescriptionTotal } from '@/services/prescription';

const { TabPane } = Tabs;

function PrescriptionManageList() {
  // 各类型订单数量
  const [tabing, setTabing] = useState(tabs[0].key);
  const [searchText, setSearchText] = useState('');
  const [listType, setListType] = useState('card');
  const [batchDeliveryIds, setBatchDeliveryIds] = useState([]);
  const [reload, setReload] = useState<any>(null);
  const [paramsRandom, setParamsRandom] = useState<any>(Date.now());
  const searchInput = useRef({
    inputVal: '',
  });
  const prescriptionTotalDataRes = useSWR<Prescription.TotalData, any>(
    'getPrescriptionTotal',
    getPrescriptionTotal,
  );
  const prescriptionTotalData = prescriptionTotalDataRes?.data || defaultPrTotalData;
  const onSearch = (val?: string) => {
    const searchValue = val || searchInput.current.inputVal;
    setParamsRandom(Date.now());
    setSearchText(searchValue);
  };
  // 查询框
  const searchBox = (
    <Card className="search-box">
      <Row gutter={24}>
        <Col span={8}>
          <Form.Item label="搜索查询" colon={false}>
            <SearchInput
              ref={searchInput}
              style={{ width: '240px' }}
              placeholder="订单编号/机构处方编号/手机号"
              value={searchText}
              onSearch={(e) => onSearch(e)}
            />
          </Form.Item>
        </Col>
        <Col span={8} offset={8} style={{ textAlign: 'right' }}>
          <Form.Item>
            <Button type="primary" onClick={() => onSearch()}>
              查询
            </Button>
          </Form.Item>
        </Col>
      </Row>
    </Card>
  );

  const onSelectTab = (e: any) => {
    setTabing(e);
  };
  const onChangeListType = (e: any) => {
    setListType(e.target.value);
  };
  const batchDeliveryFn = () => {
    Modal.confirm({
      title: '是否确定配送？',
      icon: <QuestionCircleFilled />,
      content: '',
      okText: '确定',
      cancelText: '取消',
      onOk() {
        batchDelivery({
          orderIds: batchDeliveryIds,
        }).then((data: any) => {
          // 失败的情况可能只是部分失败，仍需更新列表状态
          prescriptionTotalDataRes.mutate();
          setReload(Date.now());
          if (data.status === 'fail') {
            message.error(data.message || '配送失败');
          } else {
            message.success('配送成功');
          }
        });
      },
    });
  };
  // 表格操作栏
  const actionBar = (
    <div className={style.actionBar}>
      <SyncOutlined onClick={() => setReload(Date.now())} />
      {tabing === 'waitCount' && (
        <Radio.Group value={listType} onChange={onChangeListType}>
          <Radio.Button value="table">
            <BarsOutlined />
          </Radio.Button>
          <Radio.Button value="card">
            <TableOutlined />
          </Radio.Button>
        </Radio.Group>
      )}
      {tabing === 'waitDeliveryCount' && (
        <Button type="primary" disabled={batchDeliveryIds.length === 0} onClick={batchDeliveryFn}>
          批量配送
        </Button>
      )}
    </div>
  );
  // 各个tab下的列表
  const renderListContent = (type: string) => {
    let dom: any = null;
    if (type === 'waitCount' && listType === 'card') {
      dom = <CardList searchText={searchText} reloadRandom={reload} paramsRandom={paramsRandom} />;
    } else if (type === 'waitDeliveryCount') {
      dom = (
        <PTableList
          searchText={searchText}
          setBatchDeliveryIds={setBatchDeliveryIds}
          reloadRandom={reload}
          paramsRandom={paramsRandom}
          prescriptionTotalDataRes={prescriptionTotalDataRes}
        />
      );
    } else {
      dom = (
        <DefaultTableList
          type={type}
          searchText={searchText}
          reloadRandom={reload}
          paramsRandom={paramsRandom}
        />
      );
    }
    return useMemo(() => dom, [searchText, reload, listType, paramsRandom]);
  };
  // tabs
  const tabsBox = (
    <Tabs defaultActiveKey="1" onChange={onSelectTab} tabBarExtraContent={actionBar}>
      {tabs.map((tab) => (
        // @ts-ignore
        <TabPane tab={`${tab.label}(${prescriptionTotalData[tab.key]})`} key={tab.key}>
          <div style={{ minHeight: 'calc(100vh - 428px)' }}>{renderListContent(tab.key)}</div>
        </TabPane>
      ))}
    </Tabs>
  );
  return (
    <div>
      <PrescriptionCount totalData={prescriptionTotalData} />
      {searchBox}
      <Card>{tabsBox}</Card>
    </div>
  );
}

export default PrescriptionManageList;
