import React, { useState, useEffect } from 'react';
import { Table } from '@sinohealth/butterfly-ui-components/lib';
import useSWR from 'swr';

type TableProps = React.ComponentProps<typeof Table>;
export interface TableListProps extends TableProps {
  params?: any;
  columns: any[];
  expandAllRows?: boolean;
  expandedRowKeys?: string[];
  fetch: any;
  setMutate?: (mutate: any) => void;
  onChange?: (pagination: any, filters: any, sorter: any, extra: any) => void;
  reload?: string;
}

const TableList = (props: TableListProps) => {
  const {
    params = {},
    columns,
    expandAllRows,
    expandedRowKeys = [],
    fetch,
    reload,
    ...otherConfig
  } = props;
  const [pageNo, setPageNo] = useState<any>(1);
  const [pageSize, setPageSize] = useState<any>(10);
  useEffect(() => {
    setPageNo(1);
  }, [params]);

  const { data, mutate } = useSWR(
    {
      ...params,
      pageNo,
      pageSize,
    },
    fetch,
  );
  useEffect(() => {
    if (reload) {
      mutate();
    }
  }, [reload]);
  const onChange = (pagination: any, filters: any, sorter: any, extra: any) => {
    if (pagination.current !== pageNo) {
      setPageNo(pagination.current);
    }
    if (pagination.pageSize !== pageSize) {
      setPageSize(pagination.pageSize);
    }
    if (props.onChange) {
      props.onChange(pagination, filters, sorter, extra);
    }
  };
  return (
    <Table
      {...otherConfig}
      expandedRowKeys={expandAllRows ? data?.records.map((item: any) => item.id) : expandedRowKeys}
      columns={columns}
      dataSource={data?.records}
      pagination={{
        current: pageNo,
        pageSize,
        total: data?.total,
        // @ts-ignore
        showTotal: (total: string) => `共${total}条`,
        showSizeChanger: true,
        showQuickJumper: true,
      }}
      onChange={onChange}
      rowKey={(record: any) => record.id}
    />
  );
};

export default TableList;
