import React from 'react';
import { Card, Row, Col } from '@sinohealth/butterfly-ui-components/lib';
import style from './index.less';
import waitImg from '@/assets/images/prescription/waitIcon.svg';
import addTaday from '@/assets/images/prescription/addTaday.svg';
import addWeek from '@/assets/images/prescription/addWeek.svg';
import finishTaday from '@/assets/images/prescription/finishTaday.svg';
import finishWeek from '@/assets/images/prescription/finishWeek.svg';

type PrescriptionCountItemProps = {
  icon: React.ReactNode;
  label: string;
  number?: number;
};
function PrescriptionCountItem(props: PrescriptionCountItemProps) {
  const { icon, label, number = 0 } = props;
  return (
    <Card className={style.prescriptionCountItem}>
      <div className={style.icon}>{icon}</div>
      <div className={style.content}>
        <div>{number}</div>
        <div>{label}</div>
      </div>
    </Card>
  );
}
export type PrescriptionCountProps = {
  totalData: Prescription.TotalData | undefined;
};
function Index(props: PrescriptionCountProps) {
  const { totalData } = props;
  const prescriptionCountData = [
    {
      icon: <img src={waitImg} />,
      label: '当前待审处方',
      number: totalData?.waitOrderToday,
    },
    {
      icon: <img src={addTaday} />,
      label: '今日新增处方',
      number: totalData?.addedOrderToday,
    },
    {
      icon: <img src={finishTaday} />,
      label: '今日完结处方',
      number: totalData?.finishOrderToday,
    },
    {
      icon: <img src={addWeek} />,
      label: '本周新增处方',
      number: totalData?.addedOrderOfWeek,
    },
    {
      icon: <img src={finishWeek} />,
      label: '本周完结处方',
      number: totalData?.finishOrderOfWeek,
    },
  ];
  return (
    <Row gutter={12} justify="space-between" className={style.prescriptionCount}>
      {prescriptionCountData.map((item) => (
        <Col className="gutter-row" flex="1 0 0" key={item.label}>
          <PrescriptionCountItem icon={item.icon} label={item.label} number={item.number} />
        </Col>
      ))}
    </Row>
  );
}

export default Index;
