import React, { useState, useMemo } from 'react';
import { Link } from 'react-router-dom';
import { Modal, message } from '@sinohealth/butterfly-ui-components/lib';
import { QuestionCircleFilled } from '@ant-design/icons';
import { SWRResponse } from 'swr';
import DrugList from '@/pages/prescription/prescriptionReviewList/components/DrugList';
import TableList from '@/components/TableList';
import {
  pTableListColumns,
  searchTabType,
} from '@/pages/prescription/prescriptionReviewList/config';
import './index.less';
import { getPrescriptionList, singleDelivery } from '@/services/prescription';

function PTableList(props: {
  searchText: string;
  setBatchDeliveryIds: any;
  reloadRandom: string;
  paramsRandom: number;
  prescriptionTotalDataRes: SWRResponse;
}) {
  const { searchText, setBatchDeliveryIds, reloadRandom, paramsRandom, prescriptionTotalDataRes } =
    props;
  const [selectedRowKeys, setSelectedRowKeys] = useState<string[]>([]);
  const [reload, setReload] = useState<any>(null);
  const rowSelection = {
    selectedRowKeys,
    onChange: (selectedKeys: any) => {
      setSelectedRowKeys(selectedKeys);
      setBatchDeliveryIds(selectedKeys);
    },
  };
  const params = useMemo(
    () => ({
      paramsRandom,
      // @ts-ignore
      entity: { selectTab: searchTabType.waitDeliveryCount, simpleSearch: searchText },
      orders: [{ property: 'create_time', direction: 'asc' }],
    }),
    [searchText, paramsRandom],
  );
  const onDelivery = (orderId: string) => {
    Modal.confirm({
      title: '是否确定配送？',
      icon: <QuestionCircleFilled />,
      content: '',
      okText: '确定',
      cancelText: '取消',
      onOk() {
        singleDelivery({
          orderId,
        }).then(() => {
          prescriptionTotalDataRes?.mutate();
          message.success('配送成功');
          setReload(Date.now());
        });
      },
    });
  };
  pTableListColumns[pTableListColumns.length - 2].render = (text: string, record: any) => (
    <div>
      <a onClick={() => onDelivery(record.id)}>配送</a>
      &nbsp; &nbsp;
      <Link
        to={`/prescrip/order/reviewDetail?id=${record.id}&type=${searchTabType.waitDeliveryCount}&from=out`}
      >
        查看详情
      </Link>
    </div>
  );
  return (
    <TableList
      className="p-table"
      params={params}
      fetch={getPrescriptionList}
      expandAllRows
      columns={pTableListColumns}
      rowSelection={rowSelection}
      reload={reload + reloadRandom}
      expandable={{
        // eslint-disable-next-line react/no-unstable-nested-components,react/react-in-jsx-scope
        expandedRowRender: (item: any) => <DrugList listData={item?.drugList} />,
        expandedRowClassName: () => 'p-table-cell',
        columnWidth: 0,
      }}
    />
  );
}
export default PTableList;
